html, body, #root, .App {
  min-height: 100% !important;
  height: 100%;
}

.result-footer-selected {
  color: #0077cc;
  cursor: pointer;
}

.result-footer-selected:hover {
  color: #0095ff;
  cursor: pointer;
}

#root {
  overflow: hidden;
}

.dropzone {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2px;
  border-radius: 2px;
  border-color: #000000;
  border-style: dashed;
  background-color:#e2e2e2;
  color: #000000;
  outline: none;
  transition: border .24s ease-in-out;
}
